html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
}
body {
  background: #edeef0;
}
body,
input,
button,
select,
textarea {
  font-family: Helvetica Neue, sans-serif;
  font-size: 13px;
  outline: none;
}
.hidden {
  display: none !important;
}
a {
  color: #2a5885;
}
.btn {
  padding: 7px 16px 8px;
  cursor: pointer;
  white-space: nowrap;
  outline: none;
  text-align: center;
  background-color: #5e81a8;
  color: #fff;
  border: 0;
  border-radius: 2px;
  box-sizing: border-box;
}
.btn[disabled] {
  background: #a0b5cc;
}
.dialog {
  background: white;
  width: 550px;
  display: flex;
  flex-direction: column;
  height: 100%;
  margin: auto;
}
.dialog__header {
  border-bottom: 1px solid #e7e8ec;
  padding: 16px 10px;
  margin: 0;
  user-select: none;
  font-weight: 500;
  background: #fafbfc;
}
.dialog__close {
  float: right;
  font-size: 24px;
  margin: -8px 0;
  color: #94a3bb;
  cursor: pointer;
}
.dialog__filter {
  margin: 0 20px;
  border-bottom: 1px solid #e7e8ec;
}
.dialog__filter-icon {
  display: inline-block;
  width: 20px;
  height: 20px;
  line-height: 20px;
  vertical-align: middle;
  text-align: center;
  float: right;
  margin-top: 12px;
}
.dialog__selected-users {
  display: inline-block;
}
.dialog__input {
  border: none;
  width: 100px;
  padding: 14px 0;
  outline: none;
}
.dialog__input[fullsize="true"] {
  width: 450px;
}
.dialog__clear {
  margin-top: 10px;
  font-size: 19px;
  color: #94a3bb;
  cursor: pointer;
}
.dialog__spinner {
  background: url(images/spinner.gif) no-repeat center;
}
.dialog__friend-list {
  overflow-y: scroll;
  flex: 1;
}
.dialog__actions {
  padding: 20px;
  border-top: solid 1px #e7e8ec;
}
.dialog__submit {
  float: right;
}
.dialog__name {
  border: none;
  width: 300px;
  padding: 7px;
}
.friend {
  display: block;
  line-height: 34px;
}
.friend:hover {
  background-color: #fafbfc;
}
.friend__inner {
  padding: 10px 0;
  margin: 0 20px;
  border-bottom: 1px solid #e7e8ec;
  height: 34px;
  overflow: hidden;
}
.friend:last-child .friend__inner {
  border-bottom: none;
}
.friend__photo {
  display: inline-block;
  background-size: cover;
  border-radius: 100%;
  float: left;
  width: 34px;
  height: 34px;
}
.friend__name {
  margin: 0 10px;
  font-weight: 500;
  width: 300px;
  display: inline-block;
}
.friend__checkbox {
  float: right;
  margin: 6px 5px 0 0;
}
.user-item {
  display: inline-block;
  margin: 7px 5px 0 0;
  background-color: #dae2ea;
  border-radius: 2px;
  color: #55677d;
  cursor: pointer;
}
.user-item__name {
  display: inline-block;
  padding: 6px 10px;
  vertical-align: middle;
}
.user-item__clear {
  display: inline-block;
  vertical-align: middle;
  width: 10px;
  height: 10px;
  padding: 7px 5px;
  font-size: 18px;
  margin: -13px 5px 0 -5px;
}
.checkbox {
  display: inline-block;
  background: url(images/checkbox.png) no-repeat 0 -26px;
  width: 22px;
  height: 22px;
  background-size: 22px 48px;
}
.checkbox[checked="true"] {
  background-position: 0 0;
}



